<template>
  <div class="image-list">
    <!--    -->
    <div class="image-head">
      <el-radio-group
        v-model="isCollected"
        size="small"
        :disabled="loading"
        @change="loadImageList(1)"
      >
      <el-radio-button label="false" @click.native="isShow = true"></el-radio-button>
      <el-radio-button label="true" @click.native="isShow = false"></el-radio-button>
      </el-radio-group>
      <el-button
        v-if="isShowAdd"
        type="success"
        size="small"
        @click="dialogUploadVisible = true"
      ></el-button>
    </div>
    <!-- -() -->
    <el-row :gutter="10">
      <el-col
        class="img_list"
        v-for="(img, index) in images"
        v-loading="loading"
        :key="img.id"
        :lg="4"
        :md="6"
        :sm="6"
        :xs="12"
        @click.native="selected = index"
      >
        <el-image
          style="width: 160px; height: 160px"
          :src="img.url"
          fit="cover"
        >
        </el-image>
        <div
         v-show="isShowSelected && selected === index"
         class="selected"
        >
          <img src="./selected.png" alt="">
        </div>
        <div
          v-if="isShowOption"
          v-show="isShow"
          class="shadowOption"
        >
          <el-button
            size="medium"
            icon="el-icon-star-off"
            circle
            :loading="img.loading"
            :class="{
            'icon_btn': true,
            'icon': !img.is_collected,
            'icon_red': img.is_collected
          }"
          @click="oncolImage(img)"
          ></el-button>
          <el-button
            class="icon_btn icon"
            size="medium"
            icon="el-icon-delete"
            circle
            :loading="img.loading"
            @click="deleteImage(img)"
          ></el-button>
          <!-- <i
            :class="{
            'el-icon-star-off': true,
            'icon': !img.is_collected,
            'icon_red': img.is_collected
          }"
          @click="oncolImage(img)"
          ></i>
          <i
          class="el-icon-delete icon"
          @click="deleteImage(img.id)"></i> -->
        </div>
      </el-col>
    </el-row>
    <!--   -->
    <el-pagination
      style="margin-top: 20px"
      background
      layout="prev, pager, next"
      :total="totalCount"
      :page-size="pageSize"
      :current-page.sync="page"
      :disabled="loading"
      @current-change="oncurrentPage"
      >
    </el-pagination>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogUploadVisible"
      :append-to-body="true"
      width="30%"
      :before-close="handleClose">
      <el-upload
        class="upload-demo"
        drag
        action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
        multiple
        name="image"
        :show-file-list="true"
        :headers="uploadHeaders"
        :on-success="onUploadSuccess"
        >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
//   
//   
import {
  getImages,
  deleteImage,
  collectImage
} from '@/api/image'
export default {
  name: 'ImageList',
  data () {
    //   
    const user = JSON.parse(window.localStorage.getItem('user'))
    // console.log(user)
    return {
      images: [], // 
      isCollected: false, // 
      totalCount: null, // 
      page: 1, // 
      pageSize: 12, // 
      loading: false, // loading 
      dialogUploadVisible: false, // Dialog  
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      isShow: true, // 
      selected: null //   
    }
  },
  props: {
    //  
    isShowAdd: {
      type: Boolean, // 
      default: true
    },
    //   
    isShowOption: {
      type: Boolean,
      default: true
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  created () {
    this.loadImageList(1, false)
  },
  methods: {
    //  
    loadImageList (page = 1, isCollected = false) {
      // ,,()
      this.page = page
      //  loading 
      this.loading = true
      getImages({
        collect: this.isCollected, // 
        page, // 
        per_page: this.pageSize // 
      }).then(res => {
        // console.log(res)
        // 
        const { results } = res.data.data
        // -  loading
        results.forEach((val) => {
          val.loading = false
        })
        this.images = results
        this.totalCount = res.data.data.total_count
        //   loading 
        this.loading = false
      }).catch(err => {
        console.log(err)
      })
    },
    oncurrentPage (page) {
      // console.log(page)
      this.loadImageList(page)
    },
    handleClose (done) {
      this.$confirm('')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    // 
    onUploadSuccess () {
      //  
      this.dialogUploadVisible = false
      // 
      this.$message({
        message: '',
        type: 'success'
      })
      //  
      this.loadImageList(1)
    },
    // 
    deleteImage (img) {
      // console.log(img.id)
      //  
      img.loading = true
      deleteImage(img.id).then(res => {
        // console.log(res)
        // 
        this.$message({
          message: '',
          type: 'success'
        })
        //   
        this.loadImageList(1)
        //  
        img.loading = false
      })
    },
    // 
    oncolImage (img) {
      //  
      img.loading = true
      // 
      collectImage(img.id, !img.is_collected).then(res => {
        // console.log(res)
        //  
        img.is_collected = !img.is_collected
        // 
        this.$message({
          message: `${img.is_collected ? '' : ''}`,
          type: 'success'
        })
        //  
        img.loading = false
      })
    }
  }
}
</script>

