    height: 30px;
    background: rgba(0,0,0,.4);
    position: absolute;
    left: 5px;
    bottom: 0;
    .icon_btn {
      background: transparent;
      border: 1px solid transparent;
    }
    .icon {
      color: #fff;
    }
    .icon_red {
      color: red;
    }
  }
}
</style>
