<template>
  <el-container class="layout-container">
      <el-aside
      class="aside"
      width="auto"
      >
        <!--  -->
        <lay-aside
        class="aside-menu"
        :is-collapse="isCollapse"
        />
      </el-aside>
      <el-container>
        <el-header class="header">
            <div>
              <i :class="{
                'el-icon-s-fold': !isCollapse,
                'el-icon-s-unfold': isCollapse
              }"
              @click="isCollapse = !isCollapse"
              ></i>
              <span></span>
            </div>
            <!-- Dropdown  -->
            <el-dropdown>
              <!--    -->
              <div class="avatar-wrap">
                <img class="avatar" :src="user.photo" alt="">
                <span>{{user.name}}</span>
                <i class="el-icon-arrow-down el-icon--right"></i>
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item><i class="el-icon-setting"></i></el-dropdown-item>
                <!--  native  -->
                <el-dropdown-item
                  @click.native="onloginout"
                ><i class="el-icon-lock"></i></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
        </el-header>
        <el-main class="main">
            <!--   -->
            <router-view />
        </el-main>
      </el-container>
    </el-container>
</template>

<script>
//   
import LayAside from './components/aside.vue'
//   
import { getUserProfile } from '@/api/user'
import globalBus from '@/utils/global-bus'
export default {
  name: 'LayoutIndex',
  //  
  components: {
    LayAside
  },
  data () {
    return {
      user: {}, // 
      isCollapse: false
    }
  },
  created () {
    // ,
    this.loadUserProfile()
    //    ()
    globalBus.$on('update-user', (data) => {
      this.user.name = data.name
      this.user.photo = data.photo
    })
  },
  methods: {
    loadUserProfile () {
      getUserProfile().then((res) => {
        //  
        // console.log(res)
        this.user = res.data.data
      }).catch((err) => {
        console.log(err)
      })
    },
    onloginout () {
      this.$confirm(',?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //    
        window.localStorage.removeItem('user')
        //  
        this.$router.push('/login')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.layout-container {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    .aside {
        background-color: #002033;
        .aside-menu {
          height: 100%;
        }
    }
    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #ccc;
        .avatar-wrap {
          display: flex;
          align-items: center;
          .avatar {
            width: 35px;
            height: 35px;
            border-radius: 50%;
            margin-right: 10px;
          }
        }
    }
}
</style>
