<template>
  <div class="settings-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-row :gutter="10">
        <el-col :sm="12" :md="15" :lg="12">
          <!--  -->
          <el-form
            ref="update-form"
            :model="userInfo"
            :rules="formRules"
            label-width="80px"
          >
            <el-form-item label="">
              {{userInfo.id}}
            </el-form-item>
            <el-form-item label="">
              {{userInfo.mobile}}
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="userInfo.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="intro">
              <el-input type="textarea" v-model="userInfo.intro"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="userInfo.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                :loading="updateUserLoading"
                @click="onEditUser"
              ></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :sm="12" :md="15" :lg="6" :offset="5">
          <label for="file">
            <el-avatar
              shape="square"
              :size="200"
              fit="cover"
              :src="userInfo.photo">
            </el-avatar>
            <p></p>
          </label>
          <input
          id="file"
          type="file"
          hidden
          ref="file"
          @change="onFileChange"
          >
        </el-col>
      </el-row>
      </el-card>
      <!-- Dialog  -->
      <el-dialog
        title=""
        :visible.sync="dialogAvatarVisible"
        width="30%"
        append-to-body
        @opened="onDialogOpened"
        @closed="onDialogClosed"
      >
        <div class="preview-img-wrap">
          <img
            class="preview-img"
            :src="previewImage"
            ref="preview-img"
            alt=""
          >
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogAvatarVisible = false"> </el-button>
          <el-button
            type="primary"
            @click="onUpdateAvatar"
            :loading="updateAvatarLoading"
          > </el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import {
  getUserProfile,
  editUserPhoto,
  editUserProfile
} from '@/api/user'
import globalBus from '@/utils/global-bus'
export default {
  name: 'SettingsIndex',
  data () {
    return {
      userInfo: {
        id: null,
        name: '', // 
        email: '',
        mobile: '', // 
        photo: '', // 
        intro: '' // 
      },
      formRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, max: 60, message: ' 0  60 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^[0-9a-zA-Z_.-]+[@][0-9a-zA-Z_.-]+([.][a-zA-Z]+){1,2}$/, message: '', trigger: 'blur' }
        ]
      },
      dialogAvatarVisible: false, //  Dialog
      previewImage: '', // Dialog 
      cropper: null, // 
      updateAvatarLoading: false, //  loading
      updateUserLoading: false //  loading
    }
  },
  created () {
    this.loadUserInfo()
  },
  methods: {
    // 
    loadUserInfo () {
      getUserProfile().then(res => {
        // console.log(res)
        this.userInfo = res.data.data
      })
    },
    onFileChange () {
      //   dom
      const file = this.$refs.file
      // console.dir(file)
      //   blob url
      const blobData = window.URL.createObjectURL(file.files[0])
      // console.log(blobData)
      this.previewImage = blobData

      //  Dialog 
      this.dialogAvatarVisible = true

      //   
      this.$refs.file.value = ''
    },
    // Dialog 
    onDialogOpened () {
      const image = this.$refs['preview-img']
      // console.log(image)
      //  
      if (this.cropper) {
        //  
        this.cropper.replace(this.previewImage)
        return
      }
      this.cropper = new Cropper(image, {
        aspectRatio: 1,
        dragMode: 'none',
        cropBoxResizable: false,
        viewMode: 1,
        background: false
        //   
        // crop (event) {
        //   console.log(event.detail.x)
        //   console.log(event.detail.y)
        //   console.log(event.detail.width)
        //   console.log(event.detail.height)
        //   console.log(event.detail.rotate)
        //   console.log(event.detail.scaleX)
        //   console.log(event.detail.scaleY)
        // }
      })
    },
    onUpdateAvatar () {
      //  loading
      this.updateAvatarLoading = true
      // 
      this.cropper.getCroppedCanvas().toBlob(file => {
        //   
        const fd = new FormData()
        fd.append('photo', file)
        //  
        editUserPhoto(fd).then(res => {
          // console.log(res)
          //  
          this.dialogAvatarVisible = false
          //  blob 
          // this.userInfo.photo = window.URL.createObjectURL(file)
          this.userInfo.photo = this.previewImage
          //   ()
          // this.userInfo.photo = res.data.data.photo
          //  loading
          this.updateAvatarLoading = false
          //  
          this.$message({
            type: 'success',
            message: ''
          })
          //  --  
          globalBus.$emit('update-user', this.userInfo)
        })
      })
    },
    // 
    onEditUser () {
      //   
      this.$refs['update-form'].validate((valid) => {
        // console.log(valid)
        if (!valid) {
          return
        }
        //  loading
        this.updateUserLoading = true
        // 
        const { name, intro, email } = this.userInfo
        editUserProfile({
          name,
          intro,
          email
        }).then(res => {
          // console.log(res)
          //  loading
          this.updateUserLoading = false
          //  
          this.$message({
            type: 'success',
            message: ''
          })
          //  --  
          globalBus.$emit('update-user', this.userInfo)
        })
      })
    },
    // Dialog 
    onDialogClosed () {
      //  
      // this.cropper.destroy()
    }
  }
}
</script>

<style scoped lang="less">
.preview-img-wrap {
  .preview-img {
    display: block;

    /* This rule is very important, please don't ignore this */
    max-width: 100%;
    height: 300px;
  }
}
</style>
