<template>
  <div class="login-container">
    <div class="login-form-wrap">
      <!-- logo  -->
      <div class="login-head">
        <div class="logo">
        </div>
      </div>
      <el-form
        class="login-form"
        ref="login-form"
        :rules="formRules"
        :model="user">
        <!--  -->
        <i class="iconfont iconshoujihao"></i>
        <el-form-item prop="mobile">
          <el-input
            class="el-input--prefix"
            v-model="user.mobile"
            placeholder=""
          ></el-input>
        </el-form-item>
        <!--  -->
        <i class="iconfont iconyanzhengma"></i>
        <el-form-item prop="code">
          <el-input
          class="el-input--prefix"
          v-model="user.code"
          placeholder=""
          ></el-input>
        </el-form-item>
          <!--  -->
        <el-form-item prop="agree">
          <el-checkbox v-model="user.agree"></el-checkbox>
        </el-form-item>
          <!--  -->
        <el-form-item>
          <el-button
          type="primary"
          :loading="loginLoading"
          class="login-btn"
          @click="onLogin"
        ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { userLogin } from '@/api/user'
export default {
  name: 'LoginIndex',
  data () {
    return {
      user: {
        mobile: '13911111111', // 
        code: '246810', // 
        agree: false // 
      },
      // checked: false,
      //  
      loginLoading: false,
      formRules: {
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3,4,5,6,8,9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\d{6}$/, message: '', trigger: 'blur' }
        ],
        agree: [
          {
            //  
            validator: (rule, value, callback) => {
              // 
              // console.log(value)
              if (value) {
                // 
                callback()
              } else {
                // 
                callback(new Error(''))
              }
            },
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    onLogin () {
      this.$refs['login-form'].validate((valid) => {
        // 
        // console.log(valid)
        //   
        if (!valid) {
          return
        }
        this.login()
      })
    },
    login () {
      //  loading
      this.loginLoading = true
      userLogin(this.user).then(res => {
        // console.log(res)

        // 
        this.$message({
          message: ',',
          type: 'success'
        })

        //  loading
        this.loginLoading = false

        //     
        window.localStorage.setItem('user', JSON.stringify(res.data.data))

        //   
        this.$router.push('/')
      }).catch(err => {
        // 
        console.log('' + err)

        //  
        this.$message.error(',,')

        //  loading
        this.loginLoading = false
      })
    }
  }
}
</script>

