<template>
  <div class="article-container">
    <!--   -->
    <el-card class="filter-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!--    -->
      <el-form ref="form" :model="form" label-width="40px" size="small">
      <el-form-item label="">
        <el-radio-group v-model="status">
          <el-radio :label="null"></el-radio>
          <el-radio label="0"></el-radio>
          <el-radio label="1"></el-radio>
          <el-radio label="2"></el-radio>
          <el-radio label="3"></el-radio>
          <el-radio label="4"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="channelId" placeholder="">
          <el-option :value="null" label=""></el-option>
          <el-option v-for="item in channels" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="rangeDate"
          value-format="yyyy-MM-dd"
          type="daterange"
          range-separator=""
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
         :disabled="loading"
         @click="loadArticles(1)"
         ></el-button>
      </el-form-item>
      </el-form>
    </el-card>

    <!--   -->
    <el-card class="box-card">
      <div slot="header" class="clearfix"> {{totalCount}} :</div>
      <!--   -->
      <el-table
        :data="articlesData"
        style="width: 100%"
        class="list-table"
        v-loading="loading"
        >
        <el-table-column
          prop="date"
          label=""
        >
        <!--   -->
        <template slot-scope="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.cover.images[0]"
            fit="cover"
            lazy
            >
            <div
            slot="placeholder"
            class="image-slot"
            >
              <span class="dot">...</span>
            </div>
          </el-image>
          <!-- <img
          v-if="scope.row.cover.images[0]"
          :src="scope.row.cover.images[0]"
          class="article-cover"
          >
          <img v-else class="article-cover" src="./no-photo.gif" alt=""> -->
        </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
        >
        </el-table-column>
        <el-table-column
          label="">
          <!--  -->
          <template slot-scope="scope">
            <el-tag :type="articleStatus[scope.row.status].type">{{articleStatus[scope.row.status].text}}</el-tag>
            <!-- <el-tag v-if="scope.row.status === 0" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.status === 1"></el-tag>
            <el-tag v-else-if="scope.row.status === 2" type="success"></el-tag>
            <el-tag v-else-if="scope.row.status === 3" type="danger"></el-tag>
            <el-tag v-else-if="scope.row.status === 4" type="info"></el-tag> -->
          </template>
        </el-table-column>
        <el-table-column
          prop="pubdate"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <!--  -->
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              circle
              @click="$router.push('/publish?id=' + scope.row.id.toString())"
              ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              circle
              @click="onDeleteArticle(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        layout="prev, pager, next"
        background
        :total="totalCount"
        :page-size="pageSize"
        :disabled="loading"
        :current-page.sync="page"
        @current-change="oncurrentPage"
        >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import {
  getArticleList,
  getArticleChannels,
  deleteArticle
} from '@/api/article'
export default {
  name: 'ArticleIndex',
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      articlesData: [], // 
      articleStatus: [ //  
        { status: 0, text: '', type: 'warning' },
        { status: 1, text: '', type: '' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'danger' },
        { status: 4, text: '', type: 'info' }
      ],
      totalCount: 0, // 
      pageSize: 10, // 
      status: null, // ,
      channels: [], // 
      channelId: null, // 
      rangeDate: null, // 
      loading: true, // loading 
      page: 1 // 
    }
  },
  created () {
    this.loadArticles(1)
    this.loadChannels()
  },
  methods: {
    loadArticles (page = 1) {
      //  loading 
      this.loading = true
      getArticleList({
        page,
        per_page: this.pageSize,
        status: this.status, // 
        channel_id: this.channelId, // 
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null, // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null // 
      }).then((res) => {
        // console.log(res)
        // this.articlesData = res.data.data.results
        // this.totalCount = res.data.data.total_count
        // -(ES6)
        const { results, total_count: totalCount } = res.data.data
        this.articlesData = results
        this.totalCount = totalCount
        //   loading 
        this.loading = false
      }).catch((err) => {
        console.log(err)
      })
    },
    // 
    loadChannels () {
      getArticleChannels().then((res) => {
        // console.log(res)
        this.channels = res.data.data.channels
      }).catch((err) => {
        console.log(err)
      })
    },
    // 
    onDeleteArticle (articleId) {
      //  
      // console.log(articleId)
      // console.log(articleId.toString())
      this.$confirm(',?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //  
        deleteArticle(articleId.toString()).then((res) => {
          // 
          // console.log(res)
          // , 
          this.loadArticles(this.page)
        }).catch((err) => {
          console.log(err)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    oncurrentPage (page) {
      // console.log(page)
      this.loadArticles(page)
    }
  }
}
</script>

<style scoped lang="less">
.filter-card {
  margin-bottom: 30px;
}
.list-table {
  margin-bottom: 20px;
  .article-cover {
    width: 120px;
    background-size: contain;
  }
}
</style>
