<template>
  <div class="comment-container">
    <el-card class="box-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-table
      stripe
      :data="comments"
      v-loading="loading"
      style="width: 100%">
      <el-table-column
        prop="title"
        label=""
        >
      </el-table-column>
      <el-table-column
        prop="total_comment_count"
        label=""
        >
      </el-table-column>
      <el-table-column
        prop="fans_comment_count"
        label="">
      </el-table-column>
      <el-table-column
        prop="comment_status"
        label="">
        <template slot-scope="scope">
          {{ scope.row.comment_status ? '' : '' }}
        </template>
      </el-table-column>
      <el-table-column
        prop="comment_status"
        label=""
        >
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.comment_status"
            :disabled="scope.row.statusDisabled"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="onStatusChange(scope.row)"
            >
          </el-switch>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      style="margin-top: 20px"
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="page"
      :page-sizes="[10, 20, 30, 40, 50]"
      :page-size.sync="pageSize"
      :disabled="loading"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalCount">
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
import {
  getComments,
  editCommentStatus
} from '@/api/comment'
export default {
  name: 'CommentIndex',
  data () {
    return {
      comments: [], //  
      totalCount: 0, // 
      pageSize: 20, // 
      page: 1, // 
      loading: false
    }
  },
  created () {
    this.loadComments()
  },
  methods: {
    handleSizeChange () {
      this.loadComments(1)
    },
    handleCurrentChange (page) {
      // console.log(page)
      this.loadComments(page)
    },
    //   
    loadComments (page = 1) {
      //  loding
      this.loading = true
      //    
      this.page = page
      getComments({
        response_type: 'comment', //  comment 
        page, // 
        per_page: this.pageSize // 
      }).then((res) => {
        // console.log(res)
        // 
        const { results, total_count: totalCount } = res.data.data
        //     ()
        results.forEach(val => {
          val.statusDisabled = false
        })
        this.comments = results
        this.totalCount = totalCount
        //  loding
        this.loading = false
      })
    },
    // 
    onStatusChange (comment) {
      //  
      comment.statusDisabled = true
      editCommentStatus(comment.id.toString(), comment.comment_status).then(res => {
        // console.log(res)
        //   
        this.$message({
          message: `${comment.comment_status ? '' : ''}`,
          type: 'success'
        })
        //  
        comment.statusDisabled = false
      })
    }
  }
}
</script>

