<template>
  <div class="publish-container">
    <el-card class="box-card">
    <div slot="header" class="clearfix">
      <!--   -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- Form  -->
    <el-form ref="publish-form" :model="article" :rules="formRules" label-width="60px">
      <el-form-item label="" prop="title">
        <el-input v-model="article.title"></el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <!-- <el-input type="textarea" v-model="article.content"></el-input> -->
        <el-tiptap
          v-model="article.content"
          :height="300"
          placeholder=""
          :extensions="extensions"
        />
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="article.cover.type">
          <el-radio :label="1"></el-radio>
          <el-radio :label="3"></el-radio>
          <el-radio :label="0"></el-radio>
          <el-radio :label="-1"></el-radio>
        </el-radio-group>
        <!--   -->
        <template
          v-if="article.cover.type > 0"
        >
          <div class="article-cover">
            <upload-cover
              v-for="(cover, index) in article.cover.type"
              :key="cover"
              v-model="article.cover.images[index]"
            />
          <!-- <upload-cover
              v-for="(cover, index) in article.cover.type"
              :key="cover"
              :cover-image="article.cover.images[index]"
              @upload-imgurl="uploadImgurl(index, $event)"
          /> -->
          </div>
        </template>
      </el-form-item>
      <el-form-item label="" prop="channel_id">
        <el-select v-model="article.channel_id" placeholder="">
        <el-option
        v-for="channel in channels"
        :key="channel.id"
        :label="channel.name"
        :value="channel.id"
        ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onPublish(false)"></el-button>
        <el-button @click="onPublish(true)"></el-button>
      </el-form-item>
    </el-form>
</el-card>
  </div>
</template>

<script>
import {
  getArticleChannels,
  publishArticle,
  getArticle,
  editArticle
} from '@/api/article'
//  element-tiptap ()
//   
import UploadCover from './components/upload-cover'
import {
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  Image,
  TextColor,
  Fullscreen,
  Preview,
  Table,
  TableHeader,
  TableCell,
  TableRow,
  CodeBlock,
  FontType
} from 'element-tiptap'
// import element-tiptap 
import 'element-tiptap/lib/index.css'
//   
import { uploadImage } from '@/api/image'
export default {
  name: 'PublishIndex',
  data () {
    return {
      article: {
        title: '', // 
        content: '', // 
        cover: { // 
          type: 1, // 
          images: []
        },
        channel_id: null // id
      },
      channels: [], // 
      //  extensions
      // 
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new FontType(),
        new Bold({ bubble: true }), // 
        new Underline(), // 
        new Image({
          //  
          uploadRequest (file) {
            //   FormData 
            const fd = new FormData()
            fd.append('image', file)
            //  Promise<url>
            //  
            return uploadImage(fd).then(res => {
              // console.log(res)
              //     
              return res.data.data.url
            })
          }
        }),
        new Italic(),
        new TextColor(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new CodeBlock(),
        new Table(),
        new TableHeader(),
        new TableCell(),
        new TableRow(),
        new Preview(),
        new Fullscreen()
      ],
      // 
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 10, message: ' 5  10 ', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' },
          //  
          {
            validator (rule, value, callback) {
              if (value === '<p></p>') {
                // 
                callback(new Error(''))
              } else {
                // 
                callback()
              }
            }
          }
        ],
        channel_id: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  created () {
    this.loadChannels()
    //   id
    if (this.$route.query.id) {
      this.loadArticles()
    }
  },
  methods: {
    //  
    loadChannels () {
      getArticleChannels().then(res => {
        // console.log(res)
        this.channels = res.data.data.channels
      })
    },
    onPublish (draft) {
      //   
      this.$refs['publish-form'].validate((valid) => {
        if (!valid) {
          return
        }
        const articleId = this.$route.query.id
        //    id
        if (articleId) {
          //  
          editArticle(this.article, articleId, draft).then(res => {
            // console.log(res)
            // Message 
            this.$message({
              message: `,${draft ? '' : ''}`,
              type: 'success'
            })
            //  
            this.$router.push('/article')
          }).catch(err => {
            console.log(err)
          })
        } else {
          //  
          publishArticle(this.article, draft).then(res => {
          // console.log(res)
          // Message 
            this.$message({
              message: `,${draft ? '' : ''}`,
              type: 'success'
            })
            //   
            this.$router.push('/article')
          }).catch(err => {
            console.log(err)
          })
        }
      })
    },
    //  
    loadArticles () {
      getArticle(this.$route.query.id).then(res => {
        // console.log(res)
        this.article = res.data.data
      })
    }
    // uploadImgurl (index, url) {
    //   this.article.cover.images[index] = url
    // }
  }
}
</script>

<style scoped lang="less">
.article-cover {
  display: flex;
  justify-content: flex-start;
}
</style>
